@media screen and ( min-width: 768px)
    {


header{
    background-color: rgba(163,158,158,0.185);
    width: 100%;
    position: fixed;
    z-index: 999;

}
body{
    object-fit:fill ;
    background-image: url(/images/bg-img.jpg);
    background-position: fixed;
    background-size: 100%;
    background-repeat: no-repeat;
}

.laget{
    text-align: center;
    font-size: 1,4em;
    margin-top: 36%;
    
}

nav ul{
    text-align: right;

}

nav ul li{
    list-style-type: none;
    display: inline-block;
    padding: 10px 20px;

}
nav ul li a{
    color: white;
    text-decoration: none;
    font-weight: lighter;
}

li a::after{
    content: "";
    width: 0;
    height: 3px;
    background-color: white;
    margin: auto;
    display: block;
}

li a:hover::after{
    width: 100%;
    transition: width 0s linear;

}

section{
    flex-direction: column;
    color: white;
    padding-top: 17%;
    text-align: center;
}


.profile{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    padding: 0%;
    margin: 0%;
    list-style: none ;
    width: 500px;
    display: inline-block;
    vertical-align: top;
   }
   

